import React from 'react';
import './VideoSection.css';

const VideoSection = () => {
  return (
    <section className="video-section">
      <video className="video-bg" autoPlay loop muted>
        <source src="/videos/environment_h264.mp4" type="video/mp4" />
        Your browser does not support the video tag.
      </video>
      <div className="video-overlay">
        <div className="container text-center">
          <h1 className="display-4">Gii php tin tin cho mi trng</h1>
          <p className="lead">
            Cng ngh, ti nguyn v mi trng  ng hnh cng s pht trin bn vng
          </p>
          <a href="#cta" className="btn btn-primary">Tm hiu thm</a>
        </div>
      </div>
    </section>
  );
};

export default VideoSection;
